import city from '../../../common/city-data.json';
import { nullPass } from '../../../components/filter/index.js';
export default {
	data() {
		return {
			storeInfo: {code: '', warehouseflag : true, stopflag: false, posnonzerostockflag: true},
			cityS: [],
			isShowCity: false,
			cityType: 0,
			selPrivince: [],
			selCityInfo: [],
			selCounty: [],
			selCityNameS: {privinceName: '',cityName: '',countyArea: ''},
			selPrivinceIndex: 0,
			selCityIndex: 0,
			selCountyIndex: 0,
			moduleType: 1 ,// 0: 销售订单 1: 销售发货单
			isShowStore: false,
			storeList: [],
			storeTypeList: [],
			selStoreInfo: {},
			selStoreTypeInfo: {},
			onStoreType: 1,
			isStore: true,
			isStock: true,
			isUser: false,
			id: ''
		}
	},
	onLoad() {
		if (this.id === '' || this.id === undefined || this.id === null) {
			this.$API.generateNo().then(res => {
				this.storeInfo.code = res;
			});
		}
	},
	methods: {
		storeWitchChange (e) { // 是否店仓
			this.isStore = e.target.value;
		},
		stockWitchChange (e) { // 是否允许负库存
			this.isStock = e.target.value;
		},
		useWitchChange (e) { // 是否停用
			this.isUser = e.target.value;
		},
		onselInfo (type) { // 选择店仓类类别/类型
			this.isShowStore = true;
			this.onStoreType = type;
			uni.showLoading({
				title: '加载中...'
			});
			if (type === 1) {
				if (this.storeList.length !== 0) {
					uni.hideLoading();
					return;
				}
				this.$API.get('/fuxi/select/query-department-type').then(res => {
					uni.hideLoading();
					if (res.code === 'success') {
						this.storeList = res.data;
					}
				});
			} else if (type === 2) {
				if (this.storeTypeList.length !== 0) {
					uni.hideLoading();
					return;
				}
				this.$API.get('/fuxi/select/query-dept-type').then(res => {
					uni.hideLoading();
					if (res.code === 'success') {
						this.storeTypeList = res.data;
					}
				});
			}
			
		},
		okInfo (val) {// 确认选择店仓分类
			this.isShowStore = false;
			if (this.onStoreType === 1) {
				this.selStoreInfo = val;
			} else if (this.onStoreType === 2){
				this.selStoreTypeInfo = val;
			}
		},
		selCity (type) {
			this.cityType = type;
			if (type === 1) { // 获取省份
				this.selPrivince = city;
			} else if (type === 2) {
				if (this.selCityNameS.privinceName  === '') {
					uni.showToast({
						title: '请先选择省份',
						icon: 'none'
					});
					this.isShowCity = false;
					return;
				}
				this.selCityInfo = this.selPrivince[this.selPrivinceIndex].children;
			} else if (type === 3) {
				if (this.selCityNameS.cityName  === '') {
					uni.showToast({
						title: '请先选择城市',
						icon: 'none'
					});
					this.isShowCity = false;
					return;
				}
				this.selCounty = this.selCityInfo[this.selCityIndex].children;
			}
			this.isShowCity = true;
		},
		closeAlertCity () {
			this.isShowCity = false;
		},
		okCityValue (val, index) {
			this.isShowCity = false;
			if (this.cityType === 1) {
				this.selCityNameS.privinceName = val.values;
				this.selPrivinceIndex = index;
				this.selCityNameS.cityName = '';
				this.selCityNameS.countyArea = '';
			} else if (this.cityType === 2) {
				this.selCityNameS.cityName = val.values;
				this.selCityIndex =  index;
				this.selCityNameS.countyArea = '';
			} else 	if (this.cityType === 3) {
				this.selCityNameS.countyArea = val.values;
				this.selCountyIndex = index;
			}
		},
		closeAdd () {
			uni.navigateBack();
		},
		judgeataNull (val) {
			
		},
		save () { // 保存
			if (!nullPass(this.selStoreInfo.departmentTypeId)) {
				uni.showToast({
					title: '请选择店仓类别!',
					icon: 'none'
				});
				return;
			}
			if (!nullPass(this.storeInfo.department)) {
				uni.showToast({
					title: '店仓名称不能为空!',
					icon: 'none'
				});
				return;
			}
			let provice = nullPass(this.selCityNameS.privinceName);
			let city = nullPass(this.selCityNameS.cityName);
			let count = nullPass(this.selCityNameS.countyArea);
			if (provice && city && count) {
				this.storeInfo.city = this.selCityNameS.privinceName + '-' + this.selCityNameS.cityName + '-' + this.selCityNameS.countyArea;
			} else if (provice && city) { 
				this.storeInfo.city = this.selCityNameS.privinceName + '-' + this.selCityNameS.cityName;
			} else if (provice) {
				this.storeInfo.city = this.selCityNameS.privinceName;
			} else {
				this.storeInfo.city = '';
			}
			this.storeInfo.departmenttypeid  = this.selStoreInfo.departmentTypeId;
			this.storeInfo.depttype   = this.selStoreTypeInfo.deptType;
			this.$API.post('/fuxi/dept/add-dept', this.storeInfo).then(res => {
				if (res.code === 'success') {
					uni.showToast({
						title: '保存成功!'
					});
					this.cleaData();
				}
			});
		},
		cleaData () { // 清除数据
			this.storeInfo =  {warehouseflag : true, stopflag: false, posnonzerostockflag: true};
			this.cityS = [];
			this.isShowCity = false;
			this.cityType = 0;
			this.selPrivince = [];
			this.selCityInfo = [];
			this.selCounty = [];
			this.selCityNameS = {privinceName: '',cityName: '',countyArea: ''};
			this.selPrivinceIndex = 0;
			this.selCityIndex = 0;
			this.selCountyIndex = 0;
			this.moduleType = 1;
			this.isShowStore = false;
			this.storeList =[];
			this.selStoreInfo = {};
			this.isStore = true;
			this.isStock = true;
			this.isUser = false;
		}
	}
	
}
